table#txs-table {
  width: 100%;
  thead {
    text-align: left;
    th {
      padding: 0;
    }
    tr {
      label {
        padding: 0;
        margin-right: 10px !important;
        justify-content: flex-start;
      }
    }
  }
  tbody {
    tr {
      td {
        padding: 0 15px 0 0 !important;
      }
    }
    tr:not(.details) {
      border-top: 1px solid #CCCCCC;
      height: 50px;
      .anticon.anticon-right {
        rotate: 0deg;
        transition: 200ms linear rotate;
      }
      .anticon.tx-icon {
        margin-right: 7px;
        font-size: 16px;
        vertical-align: inherit;
      }
      &.opened {
        .anticon.anticon-right {
          rotate: 90deg;
          transition: 200ms linear rotate;
        }
      }
    }
    tr.details {
      font-size: 10px;
      line-height: 0;
      overflow: hidden;
      opacity: 0;
      transition: opacity 400ms 0ms, line-height 200ms 0ms;
      &.opened {
        display: table-row;
        line-height: 1.55;
        opacity: 1;
        transition: opacity 400ms 0ms, line-height 200ms 0ms;
        td {
          padding-bottom: 8px !important;
        }
      }
      .details-content-container {
        .details-content {
          flex-direction: column;
          &:nth-child(1) {
            margin-right: 30px;
          }
        }
      }
    }
  }
  .status {
    &__success, &__confirmed {
      color: @success-color;
    }
    &__failed, &__pending {
      color: @warning-color;
    }
  }
}

